"use client";

import { motion } from "framer-motion";
import { Heart, RefreshCw, SlidersHorizontal, Trash2 } from "lucide-react";
import Image from "next/image";
import { useApp } from "@/app/context/AppContext";
import BottomNav from "@/components/ui/BottomNav";
import LoginPage from "@/components/auth/LoginPage";
import UserHeader from "@/components/ui/UserHeader";

export default function ProfilePage() {
  const { user, favorites, filters, resetFilters, removeFavorite, clearFavorites } = useApp();

  if (!user) return <LoginPage />;

  const filterItems = [
    { title: "Style", value: filters.styles.join(", ") || "Not selected" },
    { title: "Room Type", value: filters.roomTypes.join(", ") || "Not selected" },
    { title: "Color Palette", value: filters.colors.join(", ") || "Not selected" },
    { title: "Materials", value: filters.materials.join(", ") || "Not selected" },
    { title: "Property Type", value: filters.propertyType.join(", ") || "Not selected" },
    { title: "Budget", value: filters.budget || "Not selected" },
  ];

  return (
    <div className="min-h-screen bg-[#0A0F2C] text-white px-6 pt-10 pb-28">
      
      <UserHeader />

      <motion.h1
        initial={{ opacity: 0, y: -10 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-3xl font-bold text-center mb-6"
      >
        My Profile
      </motion.h1>

      {/* –ê–∫—Ç–∏–≤–Ω—ã–µ —Ñ–∏–ª—å—Ç—Ä—ã */}
      <section className="mb-10">
        <h2 className="text-lg mb-4 font-semibold flex items-center gap-2">
          <SlidersHorizontal className="w-5 h-5 text-[#00D9FF]" />
          Active Filters
        </h2>

        <div className="space-y-3">
          {filterItems.map((item) => (
            <motion.div
              key={item.title}
              whileHover={{ scale: 1.02 }}
              className="bg-white/5 border border-white/10 rounded-xl px-4 py-3 flex justify-between"
            >
              <span className="text-gray-400">{item.title}</span>
              <span className="text-white">{item.value}</span>
            </motion.div>
          ))}
        </div>
      </section>

      {/* –ò–∑–±—Ä–∞–Ω–Ω–æ–µ */}
      <section className="mb-10">
        <h2 className="text-lg font-semibold mb-4 flex items-center gap-2">
          <Heart className="w-5 h-5 text-[#FF4D67]" />
          Liked Designs ({favorites.length})
        </h2>

        {favorites.length === 0 ? (
          <p className="text-gray-400 text-sm">No liked designs yet.</p>
        ) : (
          <>
            <div className="grid grid-cols-2 sm:grid-cols-3 gap-4 mb-4">
              {favorites.map((design) => (
                <motion.div
                  key={design.id}
                  whileHover={{ scale: 1.05 }}
                  className="relative overflow-hidden rounded-[20px]"
                >
                  <Image
                    src={design.image}
                    alt={design.roomType}
                    width={500}
                    height={500}
                    className="object-cover w-full h-40"
                  />
                  <button
                    onClick={() => removeFavorite(design.id)}
                    className="absolute top-2 right-2 bg-black/50 text-xs px-2 py-1 rounded-full"
                  >
                    Remove
                  </button>
                </motion.div>
              ))}
            </div>

            <button
              onClick={clearFavorites}
              className="w-full py-3 bg-red-500/20 border border-red-400/40 rounded-[15px] text-sm font-medium text-red-300"
            >
              Clear All Favorites
            </button>
          </>
        )}
      </section>

      {/* Reset */}
      <motion.button
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
        onClick={resetFilters}
        className="w-full py-4 bg-gradient-to-r from-[#FF4D67] to-[#FF7E89] rounded-[20px] font-semibold"
      >
        <RefreshCw className="w-5 h-5" />
        Reset Filters
      </motion.button>

      <BottomNav />
    </div>
  );
}
